import React from 'react'
import styled from 'styled-components'
import { useTable, usePagination } from 'react-table'
import { download, upload, loadData } from '../utils'
import { IndexCell, MainCell } from './cellGenerator'


const Styles = styled.div`

  table {
    width: 100%;
    border-spacing: 0;
    display: block;

    .tableWrap {
      display: block;
      max-width: 100%;
      overflow-x: scroll;
      overflow-y: hidden;
      border-bottom: 1px solid black;
    }

    tr {
      :last-child {
        td {
          border-bottom: 0;
        }
      }
    }

    th , td {
      width: 1%;
      margin: 0;
      padding: 0.3rem;
      border-bottom: 1px solid black;
      border-right: 1px solid black;

      :last-child {
        border-right: 0;
      }

      &.collapse {
        width: 0.0000000001%;
        min-width:80px;
      }

      input {
        width:100%;
        font-size: 1rem;
        padding: 0;
        margin: 0;
        border: 0;
      }

      textarea:focus, input:focus{
        outline: none;
      }
    }
  }

  .pagination {
    padding: 0.3rem;
  }
`

const columns = [
  { collapse: true, Cell: IndexCell, accessor: 'index', Header: '' },
  { collapse: true, Cell: MainCell, accessor: 'start', Header: 'Start' },
  { collapse: true, Cell: MainCell, accessor: 'end', Header: 'End' },
  { collapse: false, Cell: MainCell, accessor: 'text', Header: 'Annotation' },
]

const pageSizeOptions = [10, 15, 20, 25, 30]

function AnnoTable({ videoSeekTo }, ref) {
  const [cellFocus, setCellFocus] = React.useState()
  const [data, setData] = React.useState(Array(60).fill({ start: '', end: '', text: '' }))
  React.useImperativeHandle(ref, () => ({
    data: data,
    writeShortcutSecond: handleShortcutSecond
  }));

  const handleShortcutSecond = sec => {
    if (cellFocus && (cellFocus[1] === 'start' || cellFocus[1] === 'end'))
      updateData(cellFocus[0], cellFocus[1], sec)
  }
  const updateData = (rowIndex, columnId, value) => {
    setData(old => old.map((row, index) => {
      return index === rowIndex ? { ...old[rowIndex], [columnId]: value } : row
    }))
  }

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page,
    canPreviousPage,
    canNextPage,
    pageOptions,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize },
  } = useTable(
    {
      columns,
      data,
      autoResetPage: false,
      updateData,
      videoSeekTo,
      initialState: { pageIndex: 0, pageSize: 15 }
    },
    usePagination
  )

  return (
    <Styles>
      <div id='toolBtn-group'>
        <button onClick={() => download(data)}>Export</button>
        <button onClick={() => upload()}>Import</button>
        <input id="dataLoader" type="file" accept=".txt" onChange={()=>loadData(setData)} />
      </div>
      <div className='tableWrap'>
        <table {...getTableProps()}>
          <thead>
            {headerGroups.map(headerGroup => (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map(column => (
                  <th
                    {...column.getHeaderProps({
                      className: column.collapse ? 'collapse' : '',
                    })}
                  >
                    {column.render('Header')}
                  </th>
                ))}
              </tr>
            ))}
          </thead>
          <tbody {...getTableBodyProps()}>
            {page.map(row => {
              prepareRow(row)
              return (
                <tr {...row.getRowProps()}>
                  {row.cells.map(cell => {
                    return (
                      <td
                        {...cell.getCellProps({
                          className: cell.column.collapse
                            ? cell.column.id + '-cell collapse'
                            : cell.column.id + '-cell',
                          onFocus: () => setCellFocus([cell.row.index, cell.column.id]),
                          onBlur: () => setCellFocus(),
                        })}
                      >
                        {cell.render('Cell')}
                      </td>
                    )
                  })}
                </tr>
              )
            })}
          </tbody>
        </table>

        <div className="pagination">
          <button onClick={() => previousPage()} disabled={!canPreviousPage}>{'<'}</button>{' '}
          <button onClick={() => nextPage()} disabled={!canNextPage}>{'>'}</button>{' '}
          <span>Page{' '}<strong>{pageIndex + 1} of {pageOptions.length}</strong>{' '}</span>
          <select value={pageSize} onChange={e => { setPageSize(Number(e.target.value)) }} >
            {pageSizeOptions.map(pageSize => (
              <option key={pageSize} value={pageSize}>
                Show {pageSize}
              </option>
            ))}
          </select>
        </div>
      </div>
    </Styles>
  )
}

export default React.forwardRef(AnnoTable);
